/* Portada mejorada */
  .portada {
    background-image: url('../assets/Landing/imagenlon-2.png');
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    position: relative;
    overflow: hidden;
  }

  .portada::before {
    content: "";
    position: absolute;
    top: 0; left: 0; right: 0; bottom: 0;
    background: radial-gradient(circle at center, rgba(20, 20, 20, 0.4) 0%, rgba(20, 20, 20, 0.8) 100%);
    animation: pulse-bg 4s ease-in-out infinite;
  }

  .content-portada {
    position: relative;
    text-align: center;
    color: #f1f1f1;
    z-index: 2;
    animation: fadeInUp 1s ease-out;
  }

  @keyframes fadeInUp {
    from {
      opacity: 0;
      transform: translateY(30px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }

  .content-portada h3 {
    margin-bottom: 24px;
    color: #f7fafc;
    text-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
    font-weight: 700;
  }

  .content-portada h6 {
    margin-top: 0;
    color: #e2e8f0;
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
    line-height: 1.6;
  }

 /* Container principal mejorado - Jail Directo */
.container_jail {
  padding: 3rem 2rem;
  background: linear-gradient(135deg, #161b22 0%, #1e293b 50%, #111827 100%);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  color: #e2e8f0;
  position: relative;
  overflow: hidden;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.05);
}

/* Container Abuso de Comandos */
.container_abuso_comandos {
  padding: 3rem 2rem;
  background: linear-gradient(135deg, #1c1f24 0%, #2d3642 50%, #1a202c 100%);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.25);
  color: #e2e8f0;
  position: relative;
  overflow: hidden;
}

/* Container Abuso de Facción */
.container_abuso_faccion {
  padding: 3rem 2rem;
  background: linear-gradient(135deg, #1a202c 0%, #23403f 50%, #152524 100%);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.25);
  color: #e2e8f0;
  position: relative;
  overflow: hidden;
}

/* Container Jails Extra */
.container_jail_extra {
  padding: 3rem 2rem;
  background: linear-gradient(135deg, #0f0f0f 0%, #1c1c1e 50%, #121826 100%);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.25);
  color: #e2e8f0;
  position: relative;
  overflow: hidden;
}



  .container_abuso_comandos::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 2px;
    background: linear-gradient(90deg, #e53e3e, #42e157, #38b2ac, #e53e3e);
    background-size: 200% 100%;
    animation: shimmer 3s linear infinite;
  }
  
  .container_jail::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 2px;
    background: linear-gradient(90deg, #e53e3e, #4299e1, #38b2ac, #e53e3e);
    background-size: 200% 100%;
    animation: shimmer 3s linear infinite;
  }

  .container_abuso_faccion::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 2px;
    background: linear-gradient(90deg, #e53e3e, #292ccf, #2421ac, #e53e3e);
    background-size: 200% 100%;
    animation: shimmer 3s linear infinite;
  }
  .container_jail_extra::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 2px;
    background: linear-gradient(90deg, #7be53e, #b700ff, #ffa500, #e59d3e);
    background-size: 200% 100%;
    animation: shimmer 3s linear infinite;
  }
  
  @keyframes shimmer {
    0% { background-position: -200% 0; }
    100% { background-position: 200% 0; }
  }

  .title-jail {
    color: #f7fafc;
    font-size: 2.5rem;
    font-weight: 800;
    margin-bottom: 2rem;
    text-align: center;
    background: linear-gradient(135deg, #f7fafc 0%, #cbd5e0 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    position: relative;
  }

  .title-jail::after {
    content: "";
    position: absolute;
    bottom: -10px;
    left: 50%;
    transform: translateX(-50%);
    width: 100px;
    height: 3px;
    background: linear-gradient(90deg, #e53e3e, #4299e1);
    border-radius: 2px;
  }

  .text-content {
    font-size: 1.2rem;
    color: #cbd5e0;
    margin-bottom: 2rem;
    text-align: center;
    line-height: 1.7;
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
    font-weight: 400;
  }

  .penalties-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    gap: 2rem;
    margin-top: 2rem;
  }

  
  /* Responsive improvements */
  @media (max-width: 768px) {
    .container_jail {
      padding: 2rem 1rem;
      border-radius: 16px;
    }
    
    .title-jail {
      font-size: 2rem;
    }
    
    .text-content {
      font-size: 1.1rem;
    }
    
    .penalties-grid {
      grid-template-columns: 1fr;
      gap: 1.5rem;
    }
  }

  /* Scrollbar personalizado */
  ::-webkit-scrollbar {
    width: 8px;
  }

  ::-webkit-scrollbar-track {
    background: #1a202c;
  }

  ::-webkit-scrollbar-thumb {
    background: linear-gradient(135deg, #4a5568, #2d3748);
    border-radius: 4px;
  }

  ::-webkit-scrollbar-thumb:hover {
    background: linear-gradient(135deg, #718096, #4a5568);
  }

  @keyframes gradient-shift {
        0% { background-position: 0% 50%; }
        50% { background-position: 100% 50%; }
        100% { background-position: 0% 50%; }
      }
